import React from "react";
import Layout from "../components/Layout";
import * as styles from "../styles/404.module.css";
import image404 from "../images/404-Kedi.png";
import logo from "../images/Chefs Choice Logo.png"
import { Link } from "gatsby";


export default function NotFound() {

  // Language 
  const [language, setLanguage] = React.useState('');
  React.useEffect(() => {
    setLanguage(window.localStorage.getItem('CC-Language'));
  }, []);

  return (
    <Layout>
      <div className={styles.fourzerofour}>
          <img src={logo} alt="logo" className={styles.logo}/>
          <h1 className={styles.heading}>MEOOW!</h1>
          <p className={styles.text}>{language === "tr-TR" ? "Aradığınız sayfa sitemizde bulunmamaktadır. Geri dönüp ana sayfamıza gidebilirsiniz." : "The page that you are looking for does not exist on our site. You can go back and navigate to our homepage."} </p>
          <Link to="/" className={styles.button}>{language === "tr-TR" ? "ANA SAYFAYA DÖN" : "RETURN TO HOMEPAGE"}</Link>
      </div>
      <img src={image404} alt="cat" className={styles.image}/>
    </Layout>
  )
}